import React, { Component } from 'react';

import {
  Container, Form,
  FormGroup, Label, Input,
  Button,
} from 'reactstrap';
import  { Redirect, Link } from 'react-router-dom';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../core/services/commonService';
import Loader from '../Loader/Loader';
//import queryString from 'query-string'
import './Login.css';

class ResetPassword extends Component {
  constructor(props) {
    super(props)
    
    this.unsubscribe = null;
      this.state = {
      email: '',
      password: '',
      cpassword: '',
      userName: '',
      forgotPasswordEmail: "",
      modal: false,
      loggedIn: false,
      loading: false,      
      isLoggedIn: false,
      errors: {},
      settingsData: {},
      userInfo: {},
      userDocId: ""
    }

  }

  componentDidMount() { 
    //const params = queryString.parse(this.props.location.search);
    
    
  }

  

  submitHandler = event => {
    event.preventDefault();
    event.target.className += " was-validated";
    if(!this.validateForm())
      return false;
    this.setState({loading: true}, () => {
      //const currentObj = this;
      toast.errors("Something went wrong");
      this.setState({loading: false});
        
    });
  }
  changeHandler = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  validateForm() {
    let errors = {};
    let formIsValid = true;
    if (!this.state.password) {
        formIsValid = false;
        errors["password"] = "*Please enter new password.";
    }
    if (!this.state.cpassword) {
        formIsValid = false;
        errors["cpassword"] = "*Please enter your confirm password.";
    }
    if (this.state.password !== this.state.cpassword) {
        formIsValid = false;
        errors["cpassword"] = "*New password and confirm password does not match.";
    }
    
   
    
    this.setState({
      loading: false,
      errors: errors
    });
    
    return formIsValid;
  }


  render() {
    const { password, cpassword, loggedIn, loading,  errors} = this.state;
    if ( loggedIn ) {
      if(commonService.getLocalStorageValue('role') === "admin")
        return ( <Redirect to={`/admin`} noThrow /> )
      else
        return ( <Redirect to={`/dashboard`} noThrow /> )

    }
    return (
      <Container className="Login">
        <h2>Reset Password</h2>
        {loading ? <Loader /> : <></>}
        <ToastContainer />
        <Form className="form needs-validation" onSubmit={this.submitHandler} noValidate>
            <FormGroup>
              <Label>New Password</Label>
              <Input
                type="password"
                name="password"
                invalid={errors['password'] !== undefined && errors['password'] !== ""} value={password} onChange={this.changeHandler} id="email"
                placeholder="password"
              />
              <div className="invalid-feedback">
                {errors['password']}
              </div>
            </FormGroup>
            <FormGroup>
              <Label for="examplePassword">Confirm Password</Label>
              <Input
                type="password"
                name="cpassword"
                invalid={errors['cpassword'] !== undefined && errors['cpassword'] !== ""} value={cpassword} onChange={this.changeHandler} id="password"
                placeholder="********"
              />
              <div className="invalid-feedback">
                {errors['cpassword']}
              </div>
            </FormGroup>
            
          <Button type="submit" className="submit-btn">Submit</Button>
          <div class="col-md-6"><p class="forgot-text "><Link className="btn-view" title="Sign Up" to={`/login`}>Login</Link></p></div>
          
        </Form>
      </Container>
    );
  }
}

export default ResetPassword;